.productPage-container{
    overflow: hidden;
    width: 100vw;
    height: 100vh;
    overflow-y: auto;
}
.productPage-title-wrapper{
    width: 100vw;
    height: 110px;
    padding: 0px 120px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-bottom: 1px solid rgba(0,0,0,0.5);
}
.productPage-title{
    font-size: 3.5em;
    font-family: 'Karla',sans-serif;
    font-weight: 700;
}
.bby-header{
    width: 100%;
    height: 80px;
    padding: 0px 120px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-bottom: 1px solid rgba(0,0,0,0.5);
    z-index: 99;
}
.pp-category-wrapper{
    position: relative;
    grid-column: 3;
    width: fit-content;
    height: 100%;
    display: grid;
    cursor: pointer;
    border-left: 1px solid rgba(0,0,0,.5);
    border-right: 1px solid rgba(0,0,0,.5);
}
.product-cards-container{
    position: relative;
    width: 100%;
    height: fit-content;
    padding: 60px 120px;
    overflow: hidden;
    overflow-y: auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
}
.pp-product-container{
    position: relative;
    width: 240px;
    height: 420px;
    overflow: hidden;
    margin-bottom: 5em;
    cursor: pointer;
    background-color: #fff;
    transition: all 0.3s ease-in;
}
/* .pp-product-container{
    grid-column: 1 / span 4;
} */
.product-footer{
    width: 100%;
    height: fit-content;
    grid-column: 1 /span 4;
    bottom: 0;
}
.product-footer::before{
    content: '';
    position: absolute;
    border-top: 1px solid rgba(0,0,0,.5);
    width: 100%;
    margin-left: -120px;
}
.model-wrapper{
    z-index: 1000;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0,0,0,0.6);
    display: grid;
}
.model{
    margin: auto;
    width: 600px;
    height: 350px;
    background-color: #FFEDD2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.model-title{
    font-size: 2.5em;
    font-family: 'Antic Didone',cursive;
    margin-bottom: 0.2em;
    text-align: center;
}
.model-subtitle{
    text-align: center;
    font-size: 1.2em;
    font-family: 'Karla',sans-serif;
    margin-bottom: 1.5em;
}
.close-model{
    font-size: 2em;
    font-family: 'Karla',sans-serif;
    padding: 10px 15px;
    border: 2px solid rgb(0,0,0);
    transition: all 0.3s ease-in-out;
    cursor: pointer;
}
.close-model:hover{
    background-color: #141414;
    color: #fff;
}