.cart-wrapper{
    position: relative;
    height: 100%;
    width: 100%;
    background-color: #FFEDD2;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    z-index: 99;
    transition: all 0.3s ease-in-out;
}
.cart-title{
    height: 5%;
    margin: 1em auto;
    font-size: 2em;
    font-family: 'Karlar',sans-serif;
    font-weight: 700;
}
.cart-collection{
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 3em;
}
.cart-img-wrapper{
    overflow: hidden;
    grid-column: 1;
    width: 150px;
    height: 180px;
    margin: auto;
    background-color: #fff;
}
.cart-img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.cart-item-detail{
    margin: auto 0px;
}
.cart-item-title{
    font-size: 1.2em;
    font-weight: 400;
    font-family: 'Karla',sans-serif;
    margin-bottom: 1em;
}
.cart-item-btn{
    display: flex;
    align-items: center;
    margin-bottom: 0.5em;
}
.i-btn,.d-btn{
    padding: 5px 10px;
    border: 0.5px solid rgba(0,0,0);
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}
.i-btn:hover,.d-btn:hover,.checkout-wrapper:hover{
    background-color: #141414;
    color: white;
}
.b{
    margin-right: 0.8em;
}
.cart-item-price{
    font-size: 1.2em;
    font-weight: 700;
    font-family: 'Karla',sans-serif;
}
.total-price-wrapper{
    width: 80%;
    padding: 30px 0px;
    border-top: 1px solid rgba(0,0,0,0.5);
    border-bottom: 1px solid rgba(0,0,0,0.5);
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 2em;
}
.total-price-title{
    font-size: 2em;
    margin: auto;
    font-family: 'Karla',sans-serif;
}
.total-price{
    font-size: 2em;
    font-weight: 700;
    font-family: 'Karla',sans-serif;
    margin: auto 0px;
}
.checkout-wrapper{
    border: 1px solid rgb(0,0,0);
    padding: 20px 30px;
    margin-bottom: 2em;
    transition: all 0.3s ease-in-out;
}
.thanks-note{
    font-size: 1.5em;
    font-family: 'Karla',sans-serif;
}