.nav-wrapper{
    position: relative;
    width: 100vw;
    height: 80px;
    padding: 0px 120px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.5);
}
.nav-container{
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: max-content auto max-content max-content;
    border-left: 1px solid rgba(0, 0, 0, 0.5);
    border-right: 1px solid rgba(0, 0, 0, 0.5);
}
.logo-wrapper{
    position: relative;
    grid-column: 1;
    width: 100%;
    height: 100%;
    padding: 0px 50px;
    display: grid;
}
.logo{
    margin: auto;
    font-size: 3em;
    font-family: 'Karla';
    font-weight: 700;
}
.page-links-wrapper{
    position: relative;
    grid-column: 2;
    width: 100%;
    padding: 0px 50px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border-left: 1px solid rgba(0, 0, 0, 0.5);
    border-right: 1px solid rgba(0, 0, 0, 0.5);
}
.home,.products,.reviews{
    font-size: 1.5em;
    font-family: 'Karla'
}
.category-wrapper{
    position: relative;
    grid-column: 3;
    width: 100%;
    display: grid;
    cursor: pointer;
}
.category{
    margin: auto 50px;
    font-size: 1.5em;
    font-family: 'Karla'
}
.nav-btn{
    grid-column: 4;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-left: 1px solid rgba(0, 0, 0, 0.5);
    cursor: pointer;
}
.contacts-btn{
    font-size: 1.5em;
    font-family: 'Karla';
    margin: auto 10px;
}
.carts-btn,.wishlist-btn{
    position: relative;
    margin: auto 10px;
}
.cartItemsNo{
    position: absolute;
    top: -10px;
    right: -10px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    color: #FFEDD2;
    background-color: #141414;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Karla',sans-serif;
}
.link{
    text-decoration: none;
    color: black;
}
.cart-container{
    position: absolute;
    width: 30%;
    height: fit-content;
    min-height: 100vh;
    background-color: #FFEDD2;
    right: -2px;
    top: 80px;
    border-top: 1px solid rgba(0,0,0,0.5);
    border-left: 1px solid rgba(0,0,0,0.5);
    z-index: 99;
    transition: all 0.3s ease-in-out;
}
.cart-container-hide{
    transform: translateX(100%);
}
.cart-container-show{
    transform: translateX(0)
}