.home-container{
    position: relative;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    overflow-y: hidden;
}

.home-body-wrapper{
    width: 100vw;
    height: 100%;
    overflow-x: auto;
    scroll-behavior: smooth;
    overscroll-behavior-x: contain;
    scroll-snap-type: x mandatory;
}
.home-header-wrapper{
    width: 100%;
    height: fit-content;
}
.slide-header-title{
    width: 100%;
    font-size: 3em;
    font-family: 'Karla',sans-serif;
    font-weight: 800;
    padding: 10px 120px;
    border-bottom: 1px solid rgba(0,0,0,0.5)
}
.slide-subhead{
    width: 100%;
    padding: 0px 120px;
    border-bottom: 1px solid rgba(0,0,0,0.5);
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.sl-sh-fav,.sl-sh-cmsn{
    width: 100%;
    height: 100%;
    font-size: 2em;
    font-family: 'Karla',sans-serif;
    border: 1px solid rgba(0,0,0,0.5);
    border-top: none;
    border-bottom: none;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 0;
}
.home-body{
    position: relative;
    width: 400vw;
    height: 100%;
    display: flex;
}
.slide1,.slide2,.slide3,.slide4{
    position: relative;
    width: 25%;
    scroll-snap-align: start;
    display: flex;
    justify-content: center;
    align-items: center;
}
.slide1-title,.slide2-title,.slide3-title,.slide4-title{
    font-size: 10em;
    display: flex;
    flex-direction: column;
    width: 50%;
    z-index: 2;
    position: relative;
    top: -10%;
    user-select: none;
}
/* SLIDE ONE */
#s1-t1{
    align-self: flex-start;
}
#s1-t2{
    margin-left: 1em;
}
#s1-t3{
    align-self: flex-end;
}
/* SLIDE TWO */
#s2-t1{
    align-self: flex-end;
}
#s2-t2{
    margin-left: 3em;
}
#s2-t3{
    align-self: flex-start;
}
/* SLIDE THREE */
#s3-t1{
    align-self: center;
    margin-left: -1em;
}
#s3-t2{
    align-self: center;
    margin-left: 1em;
}
/* SLIDE FOUR */
#s4-t1,#s4-t2{
    align-self: flex-start;
}
label{
    display: flex;
    perspective-origin: 0 0;
}
.letter-wrapper{
    animation: animate 1s ease-out;
    position: relative;
    transform-style: preserve-3d;
    transform-origin: right;
    overflow: hidden;
    transition: all 1s ease-out;
}
.text{
    display: flex;
    perspective-origin: 0 0;
}
.letter{
    padding: 0px;
}
.titleAnimation{
    animation: titleAnimation 1s ease-in-out;
}
.slide-img-wrapper{
    position: absolute;
    left: 50%;
    top: 10%;
    transition: all 0.3s ease-out;
}
@keyframes animate {
    from{
        transform: rotateY(90deg);
        width: 0%;
    }
    to{
        transform: rotateY(0deg);
        width: fit-content;
    }
}
@keyframes titleAnimation {
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}

.cursor-demo{
    position: absolute;
    font-size: 2em;
    user-select: none;
    pointer-events: none;
    color: #fff;
    animation: Rotation 5s linear infinite;
}
@keyframes Rotation {
    0%{
        transform: rotate(360deg);
    }
    100%{
        transform: rotate(0deg);
    }
}
.cursor-demo span{
    position: absolute;
    top: -130px;
    text-transform: uppercase;
    display: inline-block;
    transform-origin: 0 130px;
    user-select: none;
    pointer-events: none;
}