.product-card{
    position: absolute;
    width: 240px;
    height: 420px;
    overflow: hidden;
    margin-bottom: 5em;
    cursor: pointer;
    background-color: #fff;
    transition: all 0.3s ease-in;
}
.product-card-img-wrapper{
    width: 100%;
    height: 100%;
    max-width: 240px;
    max-height: 420px;
    transition: all 0.3s ease-in;
}
.product-card-img{
    width: 100%;
    height: 100%;
    object-fit: contain;
    transition: all 0.3s ease-in-out;
}
.product-card:hover .product-card-detail-btn-wrapper{
    display: block;
}
.product-card:hover .product-card-img{
    transform: scale(1.2)
}
.price-wrapper{
    position: absolute;
    right: 20px;
    bottom: 20px;
    width: 50px;
    overflow: hidden;
    transform-origin: left;
    transition: all 0.3s ease-in;
    white-space: nowrap;
    padding: 5px 20px;
    border-radius: 50px;
    width: fit-content;
    background-color: transparent;
    border: 1px solid #000;
}
.product-card-detail-btn-wrapper{
    display: none;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    z-index: 99;
}
.product-card-detail-btn{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    padding: 10px 15px;
    border-radius: 20px;
    border: 1px solid rgb(255, 255, 255);
    color: #fff;
    transition: all 0.3s ease-in-out;
}
.product-card-detail-btn:hover{
    background-color: #FFEDD2;
    color: #000;
}