.detail-page-container{
    overflow: hidden;
    width: 100vw;
    height: 100vh;
    overflow-y: auto;
}
.product-detail-container{
    padding: 60px 120px;
    width: 100%;
    height: 60%;
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.product-detail-img-wrapper{
    width: 400px;
    height: 380px;
    overflow: hidden;
    margin: auto;
    background-color: #fff;
}
.product-detail-img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.product-detail{
    margin: auto;
    min-width: 400px;
}
.product-title{
    font-size: 3em;
    font-weight: 700;
    font-family: 'Karla',sans-serif;
    margin-bottom: 0.2em;
}
.product-description{
    font-size: 1.2em;
    font-family: 'Karla',sans-serif;
    margin-bottom: 2em;
}
.product-rating{
    font-size: 1.5em;
    font-family: 'Karla',sans-serif;
}
.r{
    font-weight: 700;
    margin-bottom: 0.2em;
}
.product-price{
    font-size: 1.5em;
    font-family: 'Karla',sans-serif;
    margin-bottom: 2em;
}
.product-cta{
    width: 100%;
    display: flex;
}
.add-to-cart-btn,.wish-list{
    width: 20%;
    border: 2px solid rgba(0,0,0);
    padding: 10px 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 1.5em;
    cursor: pointer;
    transition: all 0.5s ease-out;
}
.add-to-cart-btn:hover,
.wish-list:hover{
    background-color: #141414;
    color: #fff;
}
.detail-footer{
    width: 100%;
    padding: 0px 120px;
    border-top: 1px solid rgba(0,0,0,0.5)
}