.footer-wrapper{
    width: 100%;
    height: fit-content;
    grid-column: 1 / span 4;
    padding: 30px 0px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}
.crd-wrapper,.footer-category-wrapper,.footer-contacts-wrapper,.copyright-wrapper{
    margin-top: 2em;
}
.crd-title,.footer-category-title,.footer-contacts-title{
    font-family: 'Karla',sans-serif;
    font-size: 2em;
    font-weight: 700;
    margin-bottom: 0.5em;
}
.crd,.ft-link,.copyright{
    font-family: 'Karla',sans-serif;
    font-size: 1em;
    margin-top: 0.5em;
}